@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

#root {
  width: 100%;
  /* overflow-x: hidden; */
}

.overlay-bg {
  position: absolute;
  width: 100%;
  height: 90vh;
  background-color: black;
  z-index: 1;
  opacity: 0.5;
}

.overlay-text-container{
  position: absolute;
  top:45%;
  left:20%;
  right:20%;
  padding: 20px;
  color: white;
  border: solid;
  border-color:white;
  opacity: 1;
  border-width: thick;
  z-index: 2;
}

.overlay-text {
  font-size: 80px;
  font-weight: 700;
}

.sponsors {
  margin: 5px 0px 5px 0px;
}

.sponsors > svg {
  font-size: 2rem;
  margin: 10px 6vw 10px 6vw;
}

/* Resize the overlay-text for small screens*/
@media only screen and (max-width: 754px) {
  .overlay-text-container {
    left: 15%;
    right: 15%;
    padding: 5px;
  }
  .overlay-text {
    font-size: 40px;
  }
}

/* Resize sponsors for small screens*/
@media only screen and (max-width: 481px) {
  .sponsors > svg {
    font-size: 1.4rem;
  }
}
.confirm {
  width: 92vw;
  margin-left: 4vw;
  margin-top: 10px;
  text-align: center;
  border-radius: 10px;
  border: 1px solid gray;
}

.confirm td, .confirm th {
  padding: 10px;
}

@media only screen and (max-width: 500px) {
    .confirm {
        width: 99vw;
        margin-left: 0px;
        font-size: 15px;
    }
}
